.container_subscribe {
    background-color: #AE1332;
    max-width: 375px;
    max-height: 609px;
    margin: auto;
    padding: 0%;
}

.list {
    list-style: none;
}

.subscribe {
    background-color: #AE1332;

    &__blocks {
        width: 100%;
        height: 100%;
        position: relative;
        display: flex;
        justify-content: center;
    }

    &__images {
        top: 89px;
        position: relative;
        max-width: 335px;
        left: 20px;
        height: auto;
        justify-content: center;
        align-items: center;
        gap: 24px;
        display: flex;
        overflow: hidden;
    }

    &__image1 {
        width: 344px;
        height: 480px;
        padding: 0;
        border-radius: 20px;
        background-color: #ffffff;
        background-image: url("../images/fruit.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }

    &__image2 {
        display: none;
        width: 344px;
        height: 480px;
        padding: 0;
        border-radius: 20px;
        background-color: #ffffff;
        background-image: url("../images/blueberry.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }

    &__title {
        position: absolute;
        top: 8%;
        left: 12%;
        text-align: center;
        width: 300px;
        color: white;
        text-shadow: 0px 4px 70px rgba(0, 0, 0, 0.10);
        font-weight: 500;
        font-size: 32px;
        letter-spacing: -0.04em;
        text-transform: uppercase;
        line-height: 1;
        word-wrap: break-word;
    }

    &__block {
        position: relative;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        gap: 80px;
        display: inline-flex;
        text-align: initial;
        display: flex;
        padding-top: 15px;
        height: 400px;
    }


    &__text {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        gap: 14px;
        display: flex;
    }


    &__textOur {
        display: none;

    }

    &__input {
        width: 300px;
        height: 48px;
        position: relative;
        border-radius: 100px;
        border: 1px solid #FFF;
    }

    &__rectangle-email {
        display: none;


    }

    &__button_send {
        display: none;

    }

    &__send {
        color: white;
        font-size: 14px;
        font-family: DM Sans;
        font-weight: 400;
        text-transform: capitalize;
        line-height: 18px;
        word-wrap: break-word
    }


    &__enter-email {
        left: 24px;
        position: relative;
        opacity: 0.80;
        color: white;
        font-size: 14px;
        font-family: DM Sans;
        font-weight: 400;
        line-height: 18px;
        word-wrap: break-word;
        transform: translateY(-50%);
        top: 50%;
    }
}

//responsive

/* Estilos para tamaos de pantalla medianos (entre 768px y 1279px) */
@media screen and (min-width: 768px) {
    .container_subscribe {
        max-width: 768px;
        max-height: 965px;
        background-color: #ae1332;
        margin: auto;
        padding: 0%;
        width: 100%;
        /* Ancho al 100% para adaptarse al ancho de la pantalla */
        padding-top: 2486px;
    }

    .container--input {
        height: 278px;
        width: 100%;
    }

    .subscribe {
        background-color: #AE1332;

        &__blocks {
            height: 550px;
        }

        &__block {
            position: relative;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            gap: 80px;
            display: inline-flex;
            text-align: initial;
            display: flex;
            padding-top: 15px;
            padding-bottom: 80px;
            height: auto;
        }

        &__input {
            left: 10%;
            transform: translateX(50%);
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &__title {
            top: 0;
            font-size: 54px;
            font-weight: 500;
            line-height: 1;
        }

        &__images {
            max-width: 768px;
        }

        &__image1 {
            max-width: 344px;
            height: 480px;
        }

        &__image2 {
            display: initial;
            max-width: 344px;
            height: 480px;
        }

        &__textOur {
            display: initial;
            position: relative;
            padding-top: 80px;
            width: 492px;
            text-align: center;
            color: white;
            font-size: 14px;
            font-weight: 400;
            line-height: 18px;
            word-wrap: break-word;
        }

        &__rectangle-email {
            display: initial;
            width: 292px;
            height: 480px;
            left: 0px;
            top: 0px;
            position: relative;
            padding-bottom: 10px
        }

        &__button_send {
            display: initial;
            width: 110px;
            height: 48px;
            padding-left: 35px;
            padding-right: 35px;
            padding-top: 14px;
            padding-bottom: 15px;
            left: 190px;
            top: 0px;
            position: absolute;
            background: #9DC162;
            border-radius: 100px;
            justify-content: flex-start;
            align-items: center;
            gap: 15px;
        }

        &__title {
            top: 1%;
            left: 4%;
            width: 90%;
            font-size: 54px;
            letter-spacing: -0.04em;
        }

        /* Agregar otros ajustes de estilo para pantallas pequeas aqu */
    }
}

/* Estilos para tamaos de pantalla grandes (mayores o iguales a 1280px) */
@media screen and (min-width: 1280px) {
    .container_subscribe {
        background-color: #ae1332;
        max-width: 1280px;
        max-height: 1003px;
        margin: auto;
        padding: 0%;
        padding-top: 2600px;
    }

    .subscribe {
        &__title {
            font-size: 64px;
        }

        &__input {
            left: 25%;
            transform: translateX(50%);
        }

        &__images {
            max-width: 1280px;
            padding-bottom: 150px;
        }

        &__image1 {
            background-image: url("../images/fruit1280.jpg");
            width: 792px;
            height: 502px;
        }

        &__image2 {
            max-width: 792px;
            height: 502px;
        }
    }
}